@import url(https://fonts.googleapis.com/css?family=Open+Sans:400;600);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:400;600);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:400;600);
body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
}

:root{font-family:"Open Sans",sans-serif;font-weight:400}.app-container{align-items:center;background-color:#f3f6fa;display:flex;height:98vh;justify-content:center}
:root{font-family:"Open Sans",sans-serif;font-weight:400}.location-container{width:100%}.location-select-input{display:inline-block;width:80%}.location-select-input__option--is-selected{background-color:rgba(24,143,255,.25) !important;color:rgba(0,0,0,.7) !important}.location-select-input__control{border:0 !important;border-color:#40a9ff;border-radius:10px !important;box-shadow:0 0 7px 4px rgba(24,143,255,.1);font-size:14px;padding:3px 11px;touch-action:manipulation;transition:all .2s ease !important;width:100%}.location-select-input__control:focus-within{box-shadow:0 0 2px 2px rgba(24,143,255,.514)}.location-select-input__control:hover{box-shadow:0 0 2px 2px rgba(24,143,255,.404);width:100%}.location-select-input__menu{box-sizing:border-box;display:flex;flex-direction:column;font-size:14px;margin:0;padding:5px 12px;padding:0;position:relative}.location-select-input__option{color:rgba(0,0,0,.7);cursor:pointer;display:flex;line-height:22px;min-height:32px;transition:background .3s ease}.location-select-input__option--is-focused{background-color:rgba(24,143,255,.1) !important}.location-select-input__placeholder{border:0;color:#b9b9b9;font-weight:400;width:75%}
.location-btn,.ant-btn,.ant-btn-circle,.ant-btn-lg,.-ant-btn-icon-only{border-radius:6px;border-width:0;box-shadow:none;box-shadow:0;float:right;height:40px !important;margin-left:15px;padding:0 !important;position:absolute;vertical-align:middle;width:5% !important}.location-btn svg,.ant-btn svg,.ant-btn-circle svg,.ant-btn-lg svg,.-ant-btn-icon-only svg{color:rgba(23,135,240,.65) !important}
.left-pane-container{width:40%}
:root{font-family:"Open Sans",sans-serif;font-weight:400}.weather-forecast-container{background:#fff;border-radius:6px;box-shadow:20px 20px 20px 0 rgba(173,222,255,.5);height:400px;overflow:hidden;padding:3vh 2vw;position:relative;width:900px}
